
.inputs{
  display: flex;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
margin-top: 10px;
width: fit-content;


  justify-content: center;

  gap: 10px;
  background-color: white;


  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
form{
  display: flex;
  align-items: center;

  flex-wrap: wrap;

  justify-content: center;
  gap: 10px;
}
form input{
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  height: 40px;
  gap: 10px;
  width: 40px;
  outline: none;
  border: 1px black solid;
}
.cards{
  width: 20px;
  overflow: hidden;
  transition: 0.5s;

}
.cards:hover{
  width: 170px;


}
button {
  background-color: #04AA6D; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}
button:hover {
  background-color: #72d4b0; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}